import React from "react";
import "./Header.css";
import { Link } from "react-router-dom";

function Header() {
  return(
    <header className="header">
      <Link to="/" className="header__logo">Цирюльникъ</Link>
      <div className="header__nav">
      <Link to="" className="header__link">О нас</Link>
        <Link to="" className="header__link">Мастера</Link>
        <Link to="" className="header__link">Онлайн-запись</Link>
        <Link to="" className="header__link">Мои записи</Link>
        <Link to="" className="header__link">Личный кабинет</Link>
      </div>
    </header>
  )
}

export default Header;